.project-card{
    flex:1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 16px 0px;
    padding: 8px 0px;
}
.project-photo{
    width: 600px;
    height: 400px;
    
    
}
.project-info{
    margin-right: 24px;
}
.project-title{
    font-size: 22px;
    font-weight: 600;
}

.project-tags{
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}
.tag{
    padding: 4px 8px;
    background-color: aquamarine;
    border-radius: 4px;
    margin-right: 6px;
    margin-bottom: 4px;
}
.project-links a{
    text-decoration: none;
    color: black;
}
.project-links{
    margin-top: 6px;
    display: flex;
}
.link-botton{
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    background-color: aquamarine;
    padding: 4px 8px;
    border-radius: 4px;
    font-size: 16px;
    margin-right: 6px;
}
.link-botton i{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

@media only screen and (max-width: 720px){
    .project-photo{
        width: 70%;
    }
    .project-card{
        flex-direction: column-reverse;
    }
    .project-info{
        margin-left: 0px;
        margin-top: 12px;

    }
    .project-tags{
        max-width: 100%;
    }
}